html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1.5; }

a, ol, ul {
  list-style: none;
  text-decoration: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *:before, *:after {
  box-sizing: border-box; }

*img {
  max-width: 100%;
  height: auto; }

.menu {
  /* max-width: 1920px; 代表滿版無需寫*/
  padding: 30px 88px; }

.menu .logo {
  background: url("/img/logo-new_工作區域/logo-new_工作區域_1.png") center center;
  background-size: cover;
  display: block;
  width: 80px;
  height: 48.6px;
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap; }

.flex-row-between {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.flex-row-center {
  display: flex;
  flex-direction: row;
  align-items: center; }

.menu-list {
  max-width: 600px; }

.menu-list a {
  position: relative; }

.menu-list a:after {
  content: "";
  display: block;
  position: absolute;
  z-index: 2;
  top: -5px;
  left: 0;
  bottom: 0;
  right: 0;
  height: 3px;
  width: 0px;
  background: #fff;
  transition: 0.3s; }

.menu-list a:hover:after {
  width: 100%; }

/* // header Pad */
@media (max-width: 768px) {
  .menu {
    max-width: 768px;
    padding-left: 30px;
    padding-right: 0px; }
  .menu-list a {
    margin-right: 40px; }
  .flex-align-c {
    align-items: center; } }

/* // header mobile */
@media (max-width: 414px) {
  header.menu {
    max-width: 414px;
    margin: 0 auto;
    padding: 0px; }
  .menu .logo {
    max-width: 100%;
    height: 30px;
    margin: 10px 0px;
    background-size: contain;
    background-repeat: no-repeat; }
  .menu h1 {
    display: block; }
  .flex-col-center {
    flex-direction: column;
    align-items: center; }
  .flex-wrap-center {
    flex-wrap: wrap;
    justify-content: center; }
  .menu-list {
    width: 100%;
    /* 設置375px 時用max-width寬只能自適到308px,不確定為何 */ }
  .menu-list li {
    width: 50%;
    height: 45px;
    border: 1px solid white;
    display: flex;
    align-items: center;
    justify-content: center; }
  .menu-list li a {
    margin-right: 0px; } }
